.App {
  text-align: center;
}
.SplashApp {
  text-align: center;
  padding-top: 10%;
}

.MuiPaper-root {
  background-color: black !important;
}

.CustomApp {
  text-align: center;
  padding-left: 20%;
}

.MuiDialogContent-root {
      overflow-y: hidden !important;
}

h1 {
  color: white
}

#alert-dialog-description {
  color: white;
  padding-top: 30px;
}

#title {
  font-size: 50px;
}

.MuiButton-containedPrimary {
    background-color: grey !important;
}

.Product-wrapper {
  max-width: 900px;
  margin: 40px auto 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.graphic {
  position: absolute;
}

.tmp {
  position: relative;
  top: 0;
  left: 0;
}

.graphicList {
  height: 70%;
  width: 70%;
  padding: 1%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
